// -----------------------------------------------------------------------------
// This file contains styles that are specific to the home page.
// -----------------------------------------------------------------------------
.home-slider {
    min-height: 659px;
    height: 659px;

    .slick-list,
    .slick-track {
        height: 100%;
    }

    .home-slider-slide-first {
        background: url("../img/home/main-banner-image.jpg") no-repeat center top;
    }

    .home-slider-row {
        display: flex;
        justify-content: flex-end;
        padding-top: 192px;
    }
    
    .home-slider-column {
        // padding-right: 226px;
    }

    .home-slider-column-text {
        color: #fff;
        font-weight: 700;
        font-size: 45px;
        margin-bottom: 20px;
        // max-width: 757px;
        text-align: right;
        // line-height: 1.5;
    }

    .home-slider-button {
        margin-top: 35px;
        
        .col-12-m {
            text-align: right;
        }
    }
}

.about-us {
    position: relative;
    // height: 529px;

    .col-12-m {
        text-align: center;
        padding: 0 15px;

        h3 {
            text-transform: uppercase;
            color: #2B303A;
            font-size: 45px;
            font-weight: 700;
            padding-top: 67px;
            padding-bottom: 39px;
            margin-bottom: 0;
            line-height: 75px;
        }
    
        p {
            max-width: 991px;
            margin: 0 auto;
            padding-bottom: 17px;
            color: #2B303A;
            font-size: 20px;
            line-height: 24px;
            letter-spacing: 0;
        }
    }
}

.about-us::after {
    content: "";
    background: url("../img/home/about-image.jpg") no-repeat center center;
    background-size: cover;
    opacity: 0.2;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    position: absolute;
    z-index: -1;   
}

.our-projects__wrapper {
    position: relative;
    background: url("../img/home/our-projects-image.jpg") no-repeat center center;
    z-index: -2;

    .container-fluid,
    .row-m {
        height: 100%; 
    }

    .our-projects-overlay {
        position: absolute;
        // top: 0;
        // bottom: 0;
        // left: 0;
        // right: 0;
        height: 100%;
        width: 39%;
        background-color: rgba(43, 48, 58, 0.8);
        color: #fff;
        z-index: -1;
    }

    .our-projects-row {       
        h3 {
            color: #fff;
            text-transform: uppercase;
            font-size: 45px;
            font-weight: 700;
            // line-height: 75px;
            padding-top: 70px;
            padding-right: 54px;
            padding-bottom: 31px;
            margin-bottom: 0;
        }

        h4 {
            color: #fff;
            font-size: 24px;
            font-weight: 700;
            padding-bottom: 23px;
            margin-bottom: 0;
        }

        p {
            color: #fff;
            font-size: 20px;
            font-weight: 400;
            // line-height: 24px;
            max-width: 326px;
            padding-bottom: 133px;
            margin-bottom: 0;
        }
    }
}

.our-brands {
    margin-bottom: 57px;

    h3 {
        color: #2B303A;
        text-align: center;
        text-transform: uppercase;
        font-size: 45px;
        font-weight: 700;
        margin-top: 72px;
        margin-bottom: 43px;
    }

    .slick-track {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100% !important;
    }

    .our-brands-column {
        display: flex;
        justify-content: center;
    }
}

@media (max-width: 767px) {
    .home-slider {
        .home-slider-row {
            display: block;
            padding-top: 110px;
            
            .home-slider-column {
                text-align: center;

                .home-slider-column-text {
                    text-align: center;
                    margin-bottom: 0;
                    line-height: 1;
                }
            }
        }

        .home-slider-button {
            display: block;
            
            .col-12-m {
                text-align: center;
            }
        }
    }
}